.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

.grid__container {
  width: 100%;
  background: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);
}

.grid__element--1 {
  grid-column: 1/2;
  grid-row: 1/2;
}

.grid__element--2 {
  grid-column: 2/3;
  grid-row: 1/2;
}

.form {
  font-family: "Open Sans";
  background: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);
}

.form .far {
  color: #e17334;
  margin-right: 5px;
}

.form .form__arrow {
  color: #54585a;
}

.form .form__title {
  font-family: "Open Sans";
  font-weight: 900;
  text-transform: uppercase;
  font-size: 18px;
  color: #54585a;
  margin-bottom: 20px;
}

.form .form__closed {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.form .form__sections {
  border-bottom: 1px solid #d8d8d8;
}

.form .form__fill label {
  color: #114e4e;
}

.form .form__fill label span {
  color: #54585a;
}

.form .form__fill ::placeholder {
  color: #c5c5c5;
}

.form .form__fill input {
  border: 1px solid #a2deaf;
  margin: 5px 0 20px 0;
  height: 30px;
}

.form .form__fill .form__open {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 20px;
}

.form .form__fill .photo__upload {
  display: flex;
  flex-direction: row;
}

.form .form__fill .photo__upload--btn {
  width: 140px;
  height: 30px;
  background-color: #e17334;
  color: white;
  font-family: "Open Sans";
  font-size: 14px;
  border-radius: 3px;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 18px;
}

.form .form__fill .thumbnail {
  width: 30px;
  height: 30px;
  border: 1px solid #a2deaf;
  box-sizing: border-box;
  margin-left: 20px;
}/*--VARIABLES-------------------------*//*--MIXINS----------------------------*//*--ESTILOS GENERALES-----------------*/* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}/*--ESTILOS PREVIEW-----------------*/.page__preview {
  background: linear-gradient(#bfcdd0, #dde9ed);
  position: relative;
  width: 100%;
  height: 100vh;
}

.page__preview .preview__container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.page__preview .preview__container .preview__resetButton {
  width: 75px;
  height: 30px;
  background-color: #bfcdd0;
  border: 1px solid white;
  border-radius: 15px;
  font-family: "Open Sans";
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  color: #54585a;
  margin-bottom: 20px;
}

.page__preview .preview__container .preview__cardcontainer {
  width: 240px;
  height: 329px;
  background-color: white;
  padding-top: 21px;
  padding-bottom: 12px;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
}

.preview__profileInfo {
  border-left: 5px solid #438792;
  width: 100%;
  height: 42px;
  padding-left: 15px;
  margin-bottom: 10px;
}

.preview__profileInfo--name {
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #114e4e;
  margin-bottom: 2px;
}

.preview__profileInfo--job {
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 16px;
  color: #54585a;
  line-height: 20px;
}

.preview__pictureContainer {
  width: 100%;
  height: 200px;
  background-color: #dde9ed;
  margin-bottom: 10px;
}

.preview__socialInfo {
  padding-left: 20px;
}

.preview__socialInfo .preview__buttonList {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.preview__socialInfo .preview__buttonListitem {
  display: inline-block;
}

.preview__socialInfo .preview__buttonList .preview__socialButton {
  display: inline-block;
  position: relative;
  width: 32px;
  height: 32px;
  border: 2px solid #a2deaf;
  border-radius: 50%;
  margin-right: 10px;
  color: #114e4e;
}

.preview__socialInfo .preview__buttonList .preview__socialButton .icon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 18px;
}

@media all and (min-width: 768px) {
  .grid__container {
    padding: 20px 40px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-gap: 30px;
  }
}
