.grid__container{
    width: 100%;
    background: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);
    @include forTablet{
        padding: 20px 40px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        grid-gap: 30px;
    }
}

.grid__element--1{
grid-column: 1/2;
grid-row: 1/2;
}
.grid__element--2{
    grid-column: 2/3;
    grid-row: 1/2;    
}